<template>
	<container :width="width" :height="height">
		<h4 slot="title">TableView</h4>
		<horizontal-bar>
			<div class="btn" @click="echoCreate">
				<add title="添加" />Create a new record
			</div>
		</horizontal-bar>
		<grid :columns="columns" :objects="objects" @del="del"></grid>
		<horizontal-bar>
			<pagination :page-count="pageCount" v-model="index"></pagination>
		</horizontal-bar>
	</container>
</template>

<script>
	import Add from 'icons/add.vue';
	import Container from 'components/dialog/container';
	import HorizontalBar from './parts/horizontalBar.vue';
	import Grid from 'components/grid/index.vue';
	import Pagination from 'components/pagination/s2.vue';

	export default {
		name: 'GridView',
		data() {
			return {
				index: 0,
			};
		},
		props: {
			width: {
				type: Number,
				default() {
					return 700;
				},
			},
			height: {
				type: Number,
				default() {
					return 580;
				},
			},
			columns: {
				type: Array,
				default() {
					return [{ "field": "email", "alias": "邮箱", "width": 100, "text-align": "left" }];
				}
			},
			pageIndex: {
				type: Number,
				default() {
					return 0;
				},
			},
			pageCount: {
				type: Number,
				require: true,
				default() {
					return 1;
				},
			},
			objects: {
				type: Array,
				default() {
					return [];
				}
			},
		},
		methods: {
			echoCreate() {
				this.$emit("add");
			},
			del(e) {
				this.$emit('del',e);
			},
		},
		watch: {
			pageIndex: {
				handler(current) {
					this.index = current;
				},
			},
			index: {
				handler(current) {
					this.$emit('flip', current);
				}
			},
		},
		mounted() {
			this.index = this.pageIndex;
		},
		components: {
			Add,
			Container,
			HorizontalBar,
			Grid,
			Pagination,
		},
		model: {
			props: 'pageIndex',
			event: 'flip',
		}
	};
</script>

<style scoped>
	div.btn {
		cursor: pointer;
	}

	div.grid {
		height: calc(100% - 90px);
	}
</style>
