<template>
	<div class="container" :style="containerStyle">
		<div class="titleBar">
			<slot name="title">Container</slot>
		</div>
		<slot></slot>
	</div>
</template>

<script>
	export default {
		name: 'Container',
		props: {
			width: {
				type: Number,
				default() {
					return 700;
				},
			},
			height: {
				type: Number,
				default() {
					return 580;
				},
			},
		},
		computed: {
			containerStyle() {
				return {
					width: this.width + 'px',
					height: this.height + 'px'
				};
			},
		},
	};
</script>

<style scoped>
	div.container {
		background-color: #FFFFFF;
		border: 2px solid #999999;
	}

	div.titleBar {
		height: 30px;
		line-height: 30px;
		font-size: 14px;
		color: white;
		background-color: #999999;
	}
</style>
