<template>
	<div class="appSettingForm">
		<text-box ref="setting" v-model="setting" placeholder="名称" title="名称">
			<div class="msg" slot="tips" slot-scope="textbox">
				<p class="fl">{{textbox.tips}}</p>
			</div>
		</text-box>
		<text-box ref="val" v-model="val" placeholder="参数" title="参数">
			<div class="msg" slot="tips" slot-scope="textbox">
				<p class="fl">{{textbox.tips}}</p>
			</div>
		</text-box>
		<text-box ref="description" v-model="description" placeholder="备注" title="备注">
			<div class="msg" slot="tips" slot-scope="textbox">
				<p class="fl">{{textbox.tips}}</p>
			</div>
		</text-box>

		<btn @click.native="submit()">提交</btn>
	</div>
</template>

<script>
	import TextBox from 'components/widgets/textbox.vue';
	import Btn from 'components/widgets/button.vue';

	export default {
		name: 'AppSettingForm',
		data() {
			return {
				setting: '',
				val: '',
				description: '',
			};
		},
		methods: {
			submit() {
				this.$emit('submit', {
					setting: this.setting,
					value: this.val,
					description: this.description,
				});
			},
		},
		components: {
			TextBox,
			Btn
		},
	};

</script>

<style>
	div.appSettingForm input {
		border: 1px solid #808080;
	}

	div.msg {
		height: 20px;
	}
</style>
