<template>
	<div class="textBox">
		<input v-model="valueField" :type="type" :name="name" :maxlength="maxlength" :placeholder="placeholder" :autocomplete="autocomplete" :title="title" :style="inputStyle" @click="echoClick" @focus="echoFocus" @blur="echoBlur" />

		<slot name="tips" :tips="tips"></slot>
	</div>
</template>

<script>
	import { uuid } from 'common/helper/randomHelper';

	export default {
		name: 'TextBox',
		data() {
			return {
				valueField: '',
				tips: '',
			};
		},
		props: {
			width: {
				type: Number,
				default() {
					return 280;
				},
			},
			height: {
				type: Number,
				default() {
					return 30;
				},
			},
			value: {
				type: String | Number,
			},
			type: {
				type: String,
				default() {
					return 'text';
				},
			},
			name: {
				type: String,
				default() {
					return uuid();
				},
			},
			maxlength: {
				type: Number,
				default() {
					return 30;
				},
			},
			placeholder: {
				type: String,
				default() {
					return '';
				},
			},
			autocomplete: {
				type: String,
				default() {
					return 'off';
				},
			},
			title: {
				type: String,
				default() {
					return '';
				},
			},
			//如果匹配了该正则，check时就返回true
			pattern: {
				type: String,
				default() {
					return '';
					//return '/[<>\\[\\]{}\\\\+=]/';//eval函数碰到单\就删除，双就留一个
				},
			},
		},
		computed: {
			inputStyle() {
				return {
					'width': this.width + 'px',
					'height': this.height + 'px',
					'line-height': this.height + 'px',
				};
			},
		},
		methods: {
			echoClick(event) {
				this.$emit("click", event);
			},
			echoFocus(event) {
				this.$emit("focus", event);
			},
			echoBlur(event) {
				this.$emit("blur", event);
			},
			//验证是否为空和是否匹配正则
			check() {
				if (this.valueField.trim() != '') {
					//做正则验证
					if (this.pattern.trim() != '') {
						//console.log(eval(this.pattern));//查看正则
						if (eval(this.pattern).test(this.valueField)) {
							this.tips = '';
							return true;
						}
						else {
							this.tips = '格式不正确';
							return false;
						}
					}
					//不做正则验证
					else {
						this.tips = '';
						return true;
					}
				}
				else {
					this.tips = '请输入' + this.title;
					return false;
				}
			},
		},
		watch: {
			value: {
				handler(current) {
					this.valueField = current;//valueField是一个临时的变量，需要跟着value变化
				}
			},
			valueField: {
				handler(current) {
					this.$emit("input", current);
				}
			},
		},
	}
</script>

<style scoped>
	input {
		border: none;
		outline: none;
		border-radius: 3px;
		font-size: 15px;
	}
</style>
