		<Schedule></Schedule>

		<!--<user-list></user-list>-->
		<grid-view v-model="index" :columns="headItem" :page-count="pageCount" :objects="json" style="width:calc(100% - 20px);margin:auto" @add="btnAdd()" @del="del" />
		<dialog-window v-model="isShowAddDialog">
			<app-setting-form ref="appSettingForm" slot="content" @submit="create"></app-setting-form>
		</dialog-window>
	</div>

</template>

<script>

	//import Scroll from 'components/scroll/scroll.v2.vue';
	import Schedule from 'components/schedule/index.vue';

	//import UserList from 'components/UserList/Index.vue';

	import { requestData, createData, requestDeleteData } from 'netWork/list.js';
	import GridView from 'components/gridView/index.vue';
	import DialogWindow from 'components/dialog/index.vue';
	import AppSettingForm from './parts/appSettingForm.vue';

	export default {
		name: "Home",
		data() {
			return {
				isShow: false,
				index: 0,
				pageCount: 1,
				headItem: [
					{
						"field": "setting",
						"alias": "名称",
						"width": 100,
						"text-align": "center"
					},
					{
						"field": "value",
						"alias": "值",
						"width": 100,
						"text-align": "center"
					},
					{
						"field": "description",
						"alias": "描述",
						"text-align": "left"
					},
					{
						"field": "ord",
						"alias": "序号",
						"text-align": "center"
					},
				],
				json: [],
				isShowAddDialog: false,
			};
		},
		methods: {
			getData(n) {
				requestData(n)
					.then(res => {
						this.json = res.data;
						let pagination = JSON.parse(res.headers['x-pagination']);
						this.pageCount = pagination.pageCount;
					});
			},
			btnAdd() {
				this.isShowAddDialog = !this.isShowAddDialog;
			},
			create(dto) {
				createData(dto)
					.then(res => {
						this.$refs.appSettingForm.setting = '';
						this.$refs.appSettingForm.val = '';
						this.$refs.appSettingForm.description = '';
						this.isShowAddDialog = false;
						this.getData(this.index);
					});
			},
			del(e) {
				requestDeleteData(e)
					.then(res => this.getData(this.index + 1));
			},
			position(alias) {
				if (alias.y > 0) {
					this.isShow = true;
					//console.log(alias);
				}
				else {
					this.isShow = false;
				}
			},
			pulldown() {
				//console.log("pulldown");
				this.$refs.scroll.finishPullDown();

			},
			pullup() {
				//console.log("pullup");
				this.$refs.scroll.refresh();
				setTimeout(() => {
					this.$refs.scroll.finishPullUp();
				}, 2000);
			},
		},
		watch: {
			index: {
				handler(current) {
					this.getData(current + 1);
				},
			},
		},
		created() {
			this.getData(0);
		},
		components: {
			//Scroll,
			Schedule,
			//UserList
			GridView,
			DialogWindow,
			AppSettingForm,
		}
	};
</script>

